.icon_item {
  display: flex;
  flex-direction: column;
  justify-content: end;
  width: 150px;
  height: 150px;

  margin: 5px;

  background-repeat: no-repeat;
  background-size: cover;

  border-radius: 20px;
  border: 1px solid pink;

  cursor: pointer;
}

.icon_item:hover {
  box-shadow: inset 0 0 15px pink;
  transform: scale(1.1);
  transition: all 0.1s;
}

.icon_item:active {
  box-shadow: 0 0 15px pink;
  transform: scale(0.9);
  opacity: 0.5;
}

.icon_text {
  display: block;
  width: 85%;
  margin: 0 auto;
  text-align: center;
  border-radius: 10px;
  background-color: black;
}
