.game_info__box {
  position: absolute;
  width: 100%;
  max-width: 1756px;

  margin: 0 auto;
  border-radius: 20px;

  top: 5%;
}

.game_info__box_top {
  width: 90%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  background-color: gray;
  border-radius: 20px;
  padding: 30px 20px;

  box-shadow: 0 0 50000px 100px #262a2d;
}

.game_info__icon {
  min-width: 350px;
  min-height: 350px;

  background-repeat: no-repeat;
  background-size: cover;

  border-radius: 20px;
  border: 1px solid pink;
  box-shadow: inset 0 0 15px pink;
}

.game_info__box_text {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  margin-left: 15px;
  margin-right: 15px;
  padding: 30px 15px 10px;
  border-bottom: 2px solid seagreen;
  background-color: #262a2d;
  border-radius: 20px;
  box-shadow: 0 0 50px #262a2d;
}

.game_info__title {
  margin-bottom: 15px;
  font-size: 38px;
}

.game_info__year,
.game_info__platform {
  margin-bottom: 10px;
  font-size: 20px;
  font-weight: 700;
  color: pink;
}

.game_info__year span,
.game_info__platform span {
  color: #e0e9ee;
}

.game_info__btm_close {
  min-width: 20px;
  height: 20px;

  border: 2px solid black;
  border-radius: 50px;
  background-color: red;

  cursor: pointer;
}

.game_info__btm_close:hover {
  box-shadow: 0 0 15px red;
  transform: scale(1.1);
  transition: all 0.1s;
}

.game_info__btm_close:active {
  box-shadow: 0 0 15px red;
  transform: scale(0.9);
  opacity: 0.5;
}

.game_info__box_buttom {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 90%;
  margin: 15px auto 150px;

  background-color: #262a2d;
  border-radius: 20px;
  padding: 30px 20px;

  box-shadow: 0 0 50000px 100px #262a2d;
}

.linksYouTube__div_box_B {
  width: 48%;
  min-height: 165px;

  margin: 5px;
  padding: 15px;

  border-radius: 10px;
  background-color: grey;
  box-shadow: 0 0 50px #262a2d;
  cursor: pointer;
}

.linksYouTube__div_box_B:hover {
  box-shadow: inset 0 0 5px #262a2d;
}

.linksYouTube__div_box_B:active {
  box-shadow: 0 0 5px #262a2d;
}

.linksYouTube__div_box_L {
  display: flex;
  flex-direction: column;
  justify-content: center;

  width: 100%;
  height: 100%;
  margin: auto;

  font-weight: 700;
  font-size: 28px;
  text-align: center;

  border-radius: 5px;
  border-bottom: 2px solid seagreen;
  background-color: #262a2d;
  box-shadow: 0 0 50px #262a2d;
  cursor: pointer;
  box-shadow: inset 0 0 15px grey;
}

.linksYouTube__div_box_L:hover {
  color: red;
  border-bottom: 2px solid red;
}

.linksYouTube__div_box_L span {
  display: block;
}

@media (max-width: 1024px) {
  .game_info__icon {
    min-width: 300px;
    min-height: 300px;
  }

  .linksYouTube__div_box_B {
    padding: 12px;
  }
}

@media (max-width: 860px) {
  .game_info__box_top {
    display: block;
  }

  .game_info__icon {
    width: 300px;
    aspect-ratio: 1/1;

    margin: 0 auto 20px;
  }

  .game_info__box_text {
    width: 90%;
    margin: 0 auto 20px;
  }

  .game_info__title,
  .game_info__year,
  .game_info__platform,
  .game_info__year span,
  .game_info__platform span {
    text-align: center;
  }

  .game_info__title {
    font-size: 28px;
  }

  .game_info__btm_close {
    width: 40px;
    height: 40px;
  }

  .linksYouTube__div_box_B {
    padding: 9px;
  }
}

@media (max-width: 620px) {
  .linksYouTube__div_box_B {
    width: 95%;
  }
}

@media (max-width: 590px) {
  .game_info__title {
    font-size: 24px;
  }

  .linksYouTube__div_box_B {
    padding: 7px;
  }
}

@media (max-width: 530px) {
  .game_info__icon {
    min-width: 100px;
    min-height: 100px;
    width: 200px;
  }

  .game_info__title {
    font-size: 18px;
  }

  .game_info__year,
  .game_info__platform,
  .game_info__year span,
  .game_info__platform span {
    font-size: 14px;
  }

  .linksYouTube__div_box_B {
    padding: 6px;
  }
}

@media (max-width: 418px) {
  .game_info__title {
    font-size: 14px;
  }

  .game_info__year,
  .game_info__platform,
  .game_info__year span,
  .game_info__platform span {
    font-size: 12px;
  }

  .linksYouTube__div_box_B {
    padding: 4px;
  }
}
