.filters_box {
  width: 95%;
  max-width: 1298px;
  margin: 10px auto;
  padding: 10px 10px 20px;

  border-radius: 30px;

  background-color: #1e2225;
}

.title {
  margin: 10px 15px;
  font-size: 18px;
}

.ul_nav {
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 12px 15px;

  list-style: none;
  font-size: 14px;
  font-weight: 400;

  border-bottom: 2px solid seagreen;
}

.navPlatform__li {
  margin-right: 15px;
  margin-bottom: 7px;

  cursor: pointer;
}

.navPlatform__li:hover {
  color: pink;
}

.navPlatform__li_red {
  color: #ef202a;
}
